import React from "react";
import { graphql, useStaticQuery } from "gatsby";

import { ILang } from "../../types/common.type";
import {
  IContactFormMarkup,
  IMarkupQueryResult,
} from "../../types/commonMarkup.type";

import TemplateSection from "./templateSection";
import TextInput from "./textInput";
import TextAreaInput from "./textAreaInput";
import SelectInput from "./selectInput";
import DiscussProjectButton from "../layout/discussProjectButton";

interface IContactFromQueryResult
  extends IMarkupQueryResult<{ contactForm: IContactFormMarkup }> {
  en?: { nodes?: { html: string }[] };
  ru?: { nodes?: { html: string }[] };
}

const ContactForm = ({ lang }: ILang) => {
  const formMdData = useStaticQuery(query) as IContactFromQueryResult;
  const formMarkup = formMdData?.markupData?.nodes?.find(
    (node) => node.fields.language === lang
  )?.frontmatter?.contactForm;

  let formDescription: string | null | undefined;
  if (lang in formMdData)
    formDescription = formMdData?.[lang]?.nodes?.[0]?.html as string | null;
  else formDescription = formMdData?.en?.nodes?.[0]?.html;

  return (
    <>
      {formMarkup ? (
        <TemplateSection
          theme="light"
          title={formMarkup?.sectionTitle ?? "Let’s discuss your project"}
          linkTitle={
            formMarkup?.sectionLinkTitle ?? "we’ll be glad to hear from you!"
          }
          section="/#contactForm"
          isArrowLink={false}
        >
          <div className="flex flex-col md:flex-row gap-10 lg:gap-28 justify-center max-w-[958px] items-stretch">
            {formDescription ? (
              <p
                className="contact-form-description flex-1 text-xl font-light w-full"
                dangerouslySetInnerHTML={{ __html: formDescription }}
              />
            ) : null}
            <form action="submit" className="flex-1 flex flex-col gap-2.5">
              <TextInput
                id="email"
                type="email"
                name="email"
                label={formMarkup.emailLabel}
              />
              <TextInput
                id="companyName"
                type="text"
                name="companyName"
                label={formMarkup.companyNameLabel}
              />
              <TextInput
                id="name"
                type="text"
                name="name"
                label={formMarkup.nameLabel}
              />
              <SelectInput
                id="services"
                name="services"
                label={formMarkup.servicesLabel}
                options={[
                  "Web development",
                  "QA",
                  "Mobile development",
                  "DevOps",
                  "UX/UI",
                ]}
                placeholder={formMarkup.selectPlaceholder}
              />
              <SelectInput
                id="budget"
                name="budget"
                label={formMarkup.budgetLabel}
                options={["$5000", "$15000", "$50000"]}
                placeholder={formMarkup.selectPlaceholder}
              />
              <TextAreaInput
                name="description"
                label={formMarkup.descriptionLabel}
                placeholder={formMarkup.descriptionPlaceholder}
                placeholderIndentClass="placeholder:left-[150px]"
                rows={5}
              />
              <label className="flex items-center gap-2 pt-2.5 text-xs font-normal">
                <input type="checkbox" className="h-5 w-5 accent-main-100" />{" "}
                <span>{formMarkup.privacyPolicyCheckboxLabel}</span>
              </label>
              <label className="flex items-center gap-2 text-xs font-normal">
                <input type="checkbox" className="h-5 w-5 accent-main-100" />{" "}
                <span>{formMarkup.adsCheckboxLabel}</span>
              </label>
              <div className="flex items-center justify-center pt-5">
                <DiscussProjectButton
                  title={formMarkup.buttonLabel}
                  className="px-6"
                />
              </div>
            </form>
          </div>
        </TemplateSection>
      ) : null}
    </>
  );
};

export default ContactForm;

export const query = graphql`
  query {
    en: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "contactForm" }, language: { eq: "en" } }
      }
    ) {
      nodes {
        html
      }
    }
    ru: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "contactForm" }, language: { eq: "ru" } }
      }
    ) {
      nodes {
        html
      }
    }
    markupData: allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          contactForm {
            sectionTitle
            sectionLinkTitle
            emailLabel
            nameLabel
            companyNameLabel
            servicesLabel
            budgetLabel
            selectPlaceholder
            descriptionLabel
            descriptionPlaceholder
            privacyPolicyCheckboxLabel
            adsCheckboxLabel
            buttonLabel
          }
        }
      }
    }
  }
`;
