import React from "react";
import { graphql, useStaticQuery } from "gatsby";

import { ILang } from "../../types/common.type";
import {
  IReviewQueryResult,
  IReviewSectionQueryResult,
} from "../../types/review.type";

import TemplateSection from "../common/templateSection";
import Review from "./review";

const StaticReviewsSection = ({ lang }: ILang) => {
  const reviewsData = useStaticQuery(query) as IReviewSectionQueryResult;

  const sectionMarkup = reviewsData?.markupData?.nodes?.find(
    (node) => node.fields.language === lang
  )?.frontmatter?.reviews;

  let reviews: IReviewQueryResult[] | null | undefined;

  if (lang in reviewsData)
    reviews = reviewsData?.[lang]?.nodes?.sort((a, b) =>
      a.frontmatter.person > b.frontmatter.person ? 1 : -1
    ) as IReviewQueryResult[] | null | undefined;
  else
    reviews = reviewsData?.en?.nodes?.sort((a, b) =>
      a.frontmatter.person > b.frontmatter.person ? 1 : -1
    );

  return (
    <>
      {reviews ? (
        <TemplateSection
          id="reviews"
          theme="light"
          title={sectionMarkup?.title ?? "our clients' testimonials"}
          section="/#reviews"
          linkTitle="Real feedback from real people"
          isArrowLink={false}
          borderB
        >
          <div className="flex justify-between items-start gap-5 lg:gap-10 flex-col md:flex-row">
            {reviews.map((review) => (
              <Review review={review} key={review.id} className="flex-1" />
            ))}
          </div>
        </TemplateSection>
      ) : null}
    </>
  );
};

export default StaticReviewsSection;

export const query = graphql`
  query {
    en: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "reviews" }, language: { eq: "en" } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          company
          location
          person
          position
          image {
            childImageSharp {
              gatsbyImageData(
                height: 112
                placeholder: BLURRED
                formats: [AUTO, AVIF, WEBP]
              )
            }
          }
        }
      }
    }
    ru: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "reviews" }, language: { eq: "ru" } }
      }
    ) {
      nodes {
        id
        html
        frontmatter {
          company
          location
          person
          position
          image {
            childImageSharp {
              gatsbyImageData(
                height: 112
                placeholder: BLURRED
                formats: [AUTO, AVIF, WEBP]
              )
            }
          }
        }
      }
    }
    markupData: allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          reviews {
            title
          }
        }
      }
    }
  }
`;
