import React from "react";

interface ISelectInputProps extends React.HTMLAttributes<HTMLSelectElement> {
  name: string;
  label: string;
  options: string[];
  className?: string;
}

const SelectInput = ({
  id,
  name,
  label,
  options,
  placeholder,
  className,
  ...attr
}: ISelectInputProps) => {
  const value = "";
  const isEmptySelected = !value;

  return (
    <div className={`relative w-full h-10`}>
      <select
        disabled
        id={id}
        name={name}
        placeholder={placeholder ?? " "}
        {...attr}
        value={value}
        className={`peer h-full w-full rounded-lg border-solid border border-main-100 ${
          isEmptySelected ? "border-t-main-100" : "border-t-secondary-100"
        } px-4 py-2 text-sm font-medium text-main-100 placeholder-shown:border placeholder-shown:border-main-100 focus:border-t-secondary-100 focus:border-main-100 bg-secondary-100 placeholder:absolute focus:placeholder:left-4 placeholder:transition-all placeholder:italic placeholder:text-main-10 placeholder:font-light`}
      >
        <option hidden disabled value=""></option>
        {/* 
        {options.map((option) => (
          <option
            key={option}
            value={option}
            className="text-sm font-medium text-main-100"
          >
            {option}
          </option>
        ))} */}
      </select>

      <label
        htmlFor={id}
        className={`font-medium before:content-[' '] after:content-[' '] pointer-events-none absolute left-0 -top-[6.5px] flex h-full w-full select-none text-main-100 transition-all before:pointer-events-none before:mt-[6.5px] before:mr-1 before:box-border before:block before:h-1.5 before:w-2.5 before:rounded-tl-lg after:pointer-events-none after:mt-[6.5px] after:ml-1 after:box-border after:block after:h-1.5 after:w-2.5 after:flex-grow after:rounded-tr-lg ${
          isEmptySelected
            ? "text-sm leading-[3.9] text-main-100 before:border-0 after:border-0"
            : " leading-tight"
        }  peer-focus:text-main-100 peer-focus:text-xs peer-focus:leading-tight before:border-t before:border-l before:border-main-100 after:border-t after:border-r after:border-main-100 peer-focus:before:border-t peer-focus:before:border-l peer-focus:before:border-main-100 peer-focus:after:border-t peer-focus:after:border-r peer-focus:after:border-main-100`}
      >
        {label}
        <span
          className={`italic text-main-10 font-light pl-1 group-[.hide]:hidden ${
            !isEmptySelected ? "hidden" : ""
          }`}
        >
          {placeholder}
        </span>
      </label>
    </div>
  );
};

export default SelectInput;
