import React, { forwardRef } from "react";

import useEmblaCarousel from "embla-carousel-react";
import Autoplay from "embla-carousel-autoplay";

const StackCarousel = forwardRef<
  HTMLDivElement,
  React.PropsWithChildren<{ className?: string }>
>(({ className, children }, ref) => {
  const [emblaRef, emblaApi] = useEmblaCarousel(
    { loop: true, dragFree: true, duration: 10000, align: 0 },
    [
      Autoplay({
        delay: 0,
        stopOnInteraction: false,
        stopOnMouseEnter: true,
      }),
    ]
  );

  return (
    <div className={`overflow-hidden ${className}`} ref={emblaRef}>
      <div
        ref={ref}
        className="h-full flex items-center"
        onMouseEnter={() => {
          emblaApi?.internalEngine().animation.stop();
        }}
        onMouseLeave={() => {
          emblaApi?.internalEngine().animation.start();
        }}
      >
        {children}
      </div>
    </div>
  );
});

export default StackCarousel;
