import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import { AiFillBug } from "@react-icons/all-files/ai/AiFillBug";

import StackCarousel from "./stackCarousel";
import StackUnit from "./stakUnit";

const StackSection = () => {
  const techData = useStaticQuery(query);
  const techArr = techData?.markdownRemark?.frontmatter?.stack as
    | string[]
    | null;

  return (
    <>
      {techArr ? (
        <section className={`h-16 flex flex-col relative`}>
          <StackCarousel className="grow">
            {techArr.map((tech) => (
              <StackUnit key={tech}>{tech}</StackUnit>
            ))}
            <StackUnit>
              <AiFillBug size={32} className="-rotate-90" />
            </StackUnit>
          </StackCarousel>
        </section>
      ) : null}
    </>
  );
};

export default StackSection;

export const query = graphql`
  query {
    markdownRemark(fields: { sourceName: { eq: "stack" } }) {
      frontmatter {
        stack
      }
    }
  }
`;
